<template>
  <div class="main rule-group">
    <div class="main-table">
      <div class="table-msg">
       <div class="msg-left">
         <p>查询符合上述条件的数据<span>{{list.totalCount}}</span>条</p>
       </div>
      </div>
       <el-table
        :data="list.data"
        style="width: 100%"
        border>
        <el-table-column
          prop="dateline"
          align="center"
          :formatter="formatDate"
          label="时间">
        </el-table-column>
        <el-table-column
          prop="mid"
          align="center"
          label="表名">
        </el-table-column>
        <el-table-column
          prop="prjid_local"
          align="center"
          label="项目">
        </el-table-column>
        <el-table-column
          prop="subname"
          align="center"
          label="系统显示名">
        </el-table-column>
        <el-table-column
          prop="success"
          align="center"
          label="成功数">
        </el-table-column>
        <el-table-column
          prop="prjid_local"
          align="center"
          label="外呼团队">
        </el-table-column>
      </el-table>
     <page-table show_size="true" v-bind:cur="search.page" v-bind:total_page="list.totalPage" v-bind:total_count="list.totalCount" @change="change_data"></page-table>
    
    </div>
  </div>
</template>

<script>
import {format} from '@/config/util'
import pageTable from '@/components/pager'
import ajax from '@/config/ajax'
import Qs from 'qs'
export default {
  name: 'jobLog',
  components:{pageTable},
  data () {
    return {
      search: {
        size: 10, // 每页数量
        page: 1 // 页码
      },
      list:{
        data:[],            //数据集
        totalPage:0,        //总页数
        totalCount:0        //总条数
      },
    }
  },
  methods: {
    // 查询
    search_data () {
      this.search.page = 1
      this.getJobLogList()
    },
    change_data (index, size) {
      this.list.page = index;
      this.list.size = size;
      this.getJobLogList()
    },
    //格式化日期
    formatDate(row){
      var calldate = row.dateline.toString();
      return row.dateline = calldate.replace(/^(\d{4})(\d{2})(\d{2})$/, "$1-$2-$3");
    },
    // 获取初始化列表
    getJobLogList () {
      var params = {
        page:this.search.page,
        size:this.search.size
      }
      return ajax.request({
        method:'post',
        url:'log',
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
        },
        transformRequest: [function(params) {      //在请求之前对data传参进行格式转换
          params = Qs.stringify(params)
          return params
        }],
        data:params
      }).then((res)=>{
        console.log(res);
        this.list.data=res.data.list;
        this.list.totalCount=res.data.totalCount;
        this.list.totalPage=res.data.totalPage;
      }).catch((error)=>{
        console.log(error)
      })
    }
  },
  created () {
    this.getJobLogList();
  }
}
</script>

<style scoped>
  @import '../../assets/css/pages/main.css';
  .rule-group{
    padding-left: 15px;
  }
  .rule-group .main-table table{
    margin-bottom: 30px;
  }
</style>



